/** @jsx jsx */
import React, { useState } from "react"
import { jsx } from "theme-ui"
import styled from "@emotion/styled"
import { Link } from "gatsby"
// https://blog.logrocket.com/flexible-layouts-without-media-queries/

const LeftArrow = () => (
  <ArrowWrapper>
    <svg
      height="1em"
      width="1em"
      fill="none"
      viewBox="0 0 24 24"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
      stroke="currentColor"
    >
      <g>
        <line x1="19" y1="12" x2="5" y2="12"></line>
        <polyline points="12 19 5 12 12 5"></polyline>
      </g>
    </svg>
  </ArrowWrapper>
)
const RightArrow = () => (
  <ArrowWrapper>
    <svg
      height="1em"
      width="1em"
      fill="none"
      viewBox="0 0 24 24"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
      stroke="currentColor"
    >
      <g>
        <line x1="5" y1="12" x2="19" y2="12"></line>
        <polyline points="12 5 19 12 12 19"></polyline>
      </g>
    </svg>
  </ArrowWrapper>
)
const ArrowWrapper = styled.div`
  display: block;
  margin: 0;
  padding: 16px;
`
const SmallSection = styled.div`
  font-size: 12px;
  line-height: 1.625;
  font-weight: 400;
`
const LargeSection = styled.div`
  font-size: 16px;
  line-height: 1.5;
  font-weight: 500;
`
const NextPrevButton = styled.div`
  cursor: pointer;
  -moz-box-align: center;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  place-self: stretch;
  color: #333333;
  background-color: #fff;
  border-radius: 3px;
  border: 1px solid #e6ecf1;
  transition: border-color 0.4s ease 0s, color 0.4s ease 0s;
  box-shadow: 0 3px 8px 0 rgba(116, 129, 141, 0.1);
  text-decoration: none;
`

const NewButton = ({ children }) => (
  <NextPrevButton
    sx={{
      "&:hover": {
        color: "bulletColor",
        borderColor: "bulletColor",
      },
    }}
  >
    {children}
  </NextPrevButton>
)

function Pagination({prev, next}) {
  return (
    <>
      <div
        sx={{
          pt: ["32px", "32px", "16px"],
          display: ["block", "grid"],
          gridTemplateRows: "auto",
          columnGap: "24px",
          fontSize: "24px",
          gridTemplateColumns: "calc(50% - 8px) calc(50% - 8px);",
          
        }}
      >
        {prev !== null ? <Link className="transparent" to={prev && prev.link}>
          <NewButton>
            <LeftArrow />
            <div
              sx={{
                textAlign: "right",
                padding: "20px",
              }}
            >
              <SmallSection>
                <span>Prev</span>
              </SmallSection>
              <LargeSection>
                <span>{prev && prev.title}</span>
              </LargeSection>
            </div>
          </NewButton>
        </Link> : <div/>}
        {next !== null ? <Link className="transparent" to={next && next.link}>
          <NewButton>
            <div
              sx={{
                textAlign: "left",
                padding: "20px",
              }}
            >
              <SmallSection>
                <span>Next</span>
              </SmallSection>
              <LargeSection>
                <span>{next && next.title}</span>
              </LargeSection>
            </div>
            <RightArrow />
          </NewButton>
        </Link> : <div/>}
      </div>
    </>
  )
}

export default Pagination
