/** @jsx jsx */
import { jsx, Styled } from "theme-ui"
import React, { useState, useEffect, useRef } from "react"
import styled from "@emotion/styled"
import { graphql } from "gatsby"
import { MDXProvider } from "@mdx-js/react"
import { MDXRenderer } from "gatsby-plugin-mdx"

import HtmlHead from "../components/htmlHead"
import Layout from "../components/layout"
import Sidebar from "../components/sidebar"
import Pagination from "../components/pagination"
import TOC from "../components/toc"
import { MdxContext } from "../components/mdx/mdxContext"
import { components } from "../components/mdx"
import { getHeadingIds } from "../utils/toc"

import PhotoswipeUIDefault from "photoswipe/dist/photoswipe-ui-default"
import { CustomGallery, DefaultLayout } from "react-photoswipe-gallery"
import "photoswipe/dist/photoswipe.css"
import "photoswipe/dist/default-skin/default-skin.css"
import Footer from "../components/footer"
import { isBrowser } from "react-device-detect"
export const query = graphql`
  query($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      frontmatter {
        title
        date
      }
      body
      tableOfContents
      fields {
        dirname
      }
    }
  }
`

const ContainerBox = styled.div``

function ContentRight({ children, isOpen, isBrowser }) {
  return (
    <div
      sx={{
        display: [
          "none",
          isBrowser ? "block" : "none",
          isOpen ? "none" : "block",
          "block",
        ],
        maxWidth: ["0px", "160px", "180px", "240px", "300px"],
        mb: "50px",
      }}
    >
      {children}
    </div>
  )
}
function ContentNavigation({ children }) {
  return (
    <div
      sx={{
        position: "sticky",
        top: "15px",
        paddingLeft: "16px",
      }}
    >
      {children}
    </div>
  )
}
function ContentCenter({ children, isOpen, isBrowser }) {
  return (
    <div
      className="TULISAN"
      sx={{
        margin: "0 auto",
        minWidth: "310px",
        maxWidth: [
          "320px",
          isBrowser ? "390px" : "600px",
          isOpen ? "420px" : "490px",
          isOpen ? "440px" : "540px",
          "600px",
        ], // TULISAN
        // maxWidth: ["320px", "none", isOpen ? "600px" : "600px"],
      }}
    >
      {children}
    </div>
  )
}

const BoxWrapper = styled.div`
  display: block;
  padding: 16px;
  color: transparent;
  // border: 1px solid black;
`
function SearchBox(props) {
  return (
    <BoxWrapper
      sx={{
        display: ["none", "none", "block"],
        mt: ["0px", "0px", "40px"],
      }}
    >
      Input
    </BoxWrapper>
  )
}
function ContentLeft({ children, isOpen }) {
  const MenuContent = (
    <div
      className="SIDEBAR"
      sx={{
        display: ["block", "block", "block"],
        width: ["90vw", "90vw", "240px", "260px", "320px"],
        overflowY: "auto",
        height: [
          "calc(100vh - 55px )",
          "calc(100vh - 55px )",
          "calc(100vh - 55px - 100px)",
        ],
      }}
    >
      {children}
    </div>
  )
  return (
    <div
      sx={{
        bg: `backcolor`,
        display: "inline-block",
        zIndex: "20",
        position: "fixed",
      }}
    >
      {isOpen && (
        <div>
          <SearchBox /> {MenuContent}
        </div>
      )}
    </div>
  )
}

function Blog(props) {
  const defaultState = isBrowser ? true : false
  const [isOpen, setOpen] = useState(defaultState)
  const toggleOpen = () => {
    setOpen(!isOpen)
  }
  const mdx = props.data.mdx
  const tableOfContents = mdx.tableOfContents
  const headingIds = getHeadingIds(tableOfContents.items)
  const layoutRef = useRef()
  const tocRef = useRef()
  const dirname = props.data.mdx.fields.dirname
  const mdxContextData = {
    dirname: dirname,
  }

  const [activeHash, setActiveHash] = useState("")

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setActiveHash(entry.target.id)
        }
      },
      {
        root: null,
        rootMargin: `0% 0% -69% 0%`,
        threshold: 0.1,
      }
    )
    if (tocRef.current) {
      headingIds.forEach(id => {
        // console.log("@@@ Id nya apa")
        // console.log(id)
        // console.log("@@@ Ada Gak")
        // console.log(document.getElementById(id))
        let doc = document.getElementById(id)
        if (doc !== null) {
          observer.observe(doc)
        }
      })
    }

    return () => {
      headingIds.forEach(id => {
        let doc = document.getElementById(id)
        if (doc !== null) {
          observer.unobserve(doc)
        }
      })
    }
  }, [tocRef])

  const { prev, next } = props.pageContext
  return (
    <Layout isOpen={isOpen} toggleOpen={toggleOpen}>
      <HtmlHead title={props.data.mdx.frontmatter.title} />
      <div>
        <ContainerBox
          className="ASDA"
          ref={tocRef}
          sx={{
            marginBottom: "0",
            marginX: "auto",
            maxWidth: ["none", "none", "950px", "1100px", "1360px"], // JADI TENGAH
            // maxWidth: ["none", "none", isOpen ? "1240px" : "auto"],
          }}
        >
          <DefaultLayout
            shareButton={false}
            fullscreenButton={false}
            zoomButton={true}
            ref={layoutRef}
          />
          <CustomGallery
            layoutRef={layoutRef}
            ui={PhotoswipeUIDefault}
            id="gallery"
          >
            <MdxContext.Provider value={mdxContextData}>
              <div style={{ display: "flex" }}>
                <ContentLeft isOpen={isOpen}>
                  <Sidebar location={props.location} />
                </ContentLeft>
                <div
                  className="PENENTUAN"
                  sx={{
                    marginLeft: [
                      "auto",
                      "auto",
                      isOpen && "240px",
                      isOpen && "260px",
                      isOpen && "320px",
                    ],
                    marginRight: ["auto", "auto", "auto", "auto"],
                    paddingX: ["8px", "8px", "28px"],
                  }}
                >
                  <div
                    sx={{
                      margin: "0 auto",
                      paddingX: ["8px", "8px", "16px"],
                    }}
                  >
                    <div style={{ display: "flex" }}>
                      <ContentCenter isOpen={isOpen} isBrowser={isBrowser}>
                        <div
                          sx={{
                            paddingTop: ["2rem", "2rem", "4rem"],
                          }}
                        >
                          <Styled.h1 className="container">
                            {props.data.mdx.frontmatter.title}
                          </Styled.h1>
                        </div>
                        <MDXProvider components={components}>
                          <MDXRenderer>{mdx.body}</MDXRenderer>
                        </MDXProvider>
                      </ContentCenter>
                      <ContentRight isOpen={isOpen} isBrowser={isBrowser}>
                        <ContentNavigation>
                          <TOC
                            items={tableOfContents.items}
                            activeHash={activeHash}
                          />
                        </ContentNavigation>
                      </ContentRight>
                    </div>
                  </div>
                  <Pagination prev={prev} next={next} />
                  <Footer />
                </div>
              </div>
            </MdxContext.Provider>
          </CustomGallery>
        </ContainerBox>
      </div>
    </Layout>
  )
}

export default Blog
