/** @jsx jsx */
import { jsx } from "theme-ui"
import { Link } from "gatsby"

function createItems(items, activeHash) {
  if (items === undefined) {
    return null
  }
  const sections = items.map((item, index) => {
    const isActive = item.url.slice(1) === activeHash
    const origin = item.title.split(":::")
    const shortTitle = origin[1] ?? origin[0]
    return (
      <li>
        <Link
          sx={{
            // transition: "color .1s ease, font-weight .1s ease",
            minHeight: `sidebarItemMinHeight`,
            position: `relative`,
            py: 3,
            width: `100%`,
            zIndex: 1,
          }}
          to={item.url}
        >
          <span
            sx={{
              fontSize: ["11px", "14px", "15px", "17px"],
              color: isActive ? "bulletColor" : "textMuted",
              fontWeight: isActive ? "semiBold" : "text",
              "&:hover": {
                fontWeight: "900",
                color: "bulletColor",
              },
            }}
          >
            {shortTitle}
          </span>
        </Link>
        {item.items && (
          <ul
            sx={{
              listStyle: "none",
            }}
          >
            {createItems(item.items, activeHash)}
          </ul>
        )}
      </li>
    )
    return (
      <li className={linkClass}>
        <Link to={item.url}>{shortTitle}</Link>
        {item.items && <ul>{createItems(item.items, activeHash)}</ul>}
      </li>
    )
  })

  return <li>{sections}</li>
}
function Toc({ items, activeHash }) {
  if (items === undefined) {
    return null
  }
  return (
    <div>
      <div
        sx={{
          pl: 6,
          py: 3,
          pt: ["20px","20px","96px"],
          fontSize: ["11px", "11px", "12px", ],
          fontWeight: "heading",
          color: "textMuted",
        }}
      >
        TABLE OF CONTENTS
      </div>
      <div>
        <ul sx={{ listStyle: "none" }}>{createItems(items, activeHash)}</ul>
      </div>
    </div>
  )
}

export default Toc
