function Accordion({ itemRef, item }) {
  const { getItemState, disableAccordions } = useSidebarContext()
  const { inActiveTree, isExpanded } = getItemState(item)

  // TODO use the useUniqueId hook when React releases it
  // https://github.com/facebook/react/pull/17322
  // Use the title as the ID since it's already being used as the hash key
  const uid = `item_${item.title.replace(/[^-a-zA-Z0-9]+/g, `_`)}`

  return (
    <li
      className="BAB"
      sx={{
          fontSize: ["14px", "14px", "14px", "16px"],

        bg:
          item.level === 0 && inActiveTree && `sidebar.activeSectionBackground`,
        position: `relative`,
        transition: t =>
          `all ${t.transition.speed.fast} ${t.transition.curve.default}`,
        mt: t =>
          item.level === 0 && disableAccordions && `${t.space[4]} !important`,
        ...(item.level === 0 && {
          "::before": {
            content: `" "`,
            position: `absolute`,
            borderTopWidth: `1px`,
            borderTopStyle: `solid`,
            borderColor: `ui.border`,
            left: isExpanded && inActiveTree ? 0 : 6,
            right: 0,
            top: 0,
          },
          ":after": {
            top: `auto`,
            bottom: -1,
          },
        }),
      }}
    >
      <SectionTitle itemRef={itemRef} item={item} uid={uid} />
      {isExpanded && (
        <ul
          id={uid}
          sx={{
          fontSize: ["14px", "14px", "14px", "16px"],

            listStyle: `none`,
            margin: 0,
            position: `relative`,
            ...(item.ui === `steps` && {
              "&:after": {
                backgroundColor: `ui.border`,
                bottom: 0,
                content: `''`,
                left: 27,
                position: `absolute`,
                top: 0,
                width: 1,
              },
            }),
          }}
        >
          {item.items.map(subitem => (
            <Item
              item={subitem}
              key={subitem.title}
              isSteps={item.ui === `steps`}
            />
          ))}
        </ul>
      )}
    </li>
  )
}

const remToPx = rem => parseFloat(rem) * 16
const pxToRem = pixels => `${pixels / 16}rem`

const indention = level => {
  let hasil
  if (level === 0 || level === 1) {
    hasil = "1.5rem"
  } else {
    hasil = pxToRem(level * remToPx("1.5rem"))
  }
  hasil = pxToRem(remToPx(hasil) + 10)
  return hasil
}

const indention2 = level =>
  level === 0 || level === 1 ? "1.5rem" : pxToRem(level * remToPx("1.5rem"))

const NewLink = ({ to, ...props }) => {
  return <Link {...props} to={to} />
}

const _getTitle = (title, isDraft) => (isDraft ? title.slice(0, -1) : title)
const _isDraft = title => title.slice(-1) === `*`

const bulletSize = 8
const bulletSizeActive = 100
// const bulletOffsetTop = `1.05rem`
const bulletOffsetTop = `1.5rem`

function ItemLink({ item, overrideCSS, isSteps }) {
  const { onLinkClick, getItemState } = useSidebarContext()
  const { isActive, inActiveTree } = getItemState(item)

  const isDraft = _isDraft(item.title)
  const title = _getTitle(item.title, isDraft)

  const level = item.level
  const indent = isSteps ? indention(level + 1) : indention(level + 1)

  return (
    <span
      sx={{
        alignItems: `center`,
        display: `flex`,
        position: `relative`,
        "&:before": {
          bg: `sidebar.itemBorderColor`,
          bottom: 0,
          content: `''`,
          height: 1,
          left: indent,
          position: `absolute`,
          right: 0,
          top: `auto`,
        },
      }}
    >
      <NewLink
        sx={{
          minHeight: `sidebarItemMinHeight`,
          position: `relative`,
          pl: indent,
          pr: 4,
          py: 3,
          textDecoration: `none`,
          width: `100%`,
          zIndex: 1,
          "&&": {
            border: 0,
            color: `navigation.linkDefault`,
            fontWeight: `body`,
            ...(isDraft && {
              color: `textMuted`,
            }),
            ...(inActiveTree && {
              color: `link.color`,
              fontWeight: `semiBold`,
            }),
            "&:hover": {
              bg: `sidebar.itemHoverBackground`,
              color: `navigation.linkHover`,
              "&:before": {
                bg: `bulletColor`,
                // transform: `scale(1)`,
                transform: `translateX(-${25 - bulletSize}px)`,
              },
            },
            ...overrideCSS,
          },
          "&:before, &:after": {
            content: `''`,
            left: t =>
              level === 0 || (level === 1 && !isSteps)
                ? `calc(${indent} - ${t.space[4]})`
                : `calc(${indent} - ${t.space[6]})`,
            top: bulletOffsetTop,
            height: bulletSize,
            position: `absolute`,
            transition: `default`,
            width: bulletSize,
          },
          "&:before": {
            bg: isActive && `bulletColor`,
            borderRadius: 6,
            transform: `translateX(-${30 - bulletSize}px)`,
            // transform: isActive ? `scale(1)` : `scale(0.1)`,
          },
        }}
        onClick={onLinkClick}
        to={item.link}
      >
        {isSteps && (
          <span
            sx={{
              bg: `ui.border`,
              borderColor: `ui.border`,
              borderRadius: 6,
              borderStyle: `solid`,
              borderWidth: `1px`,
              display: `block`,
              fontWeight: `body`,
              height: bulletSize,
              left: 6,
              position: `absolute`,
              top: bulletOffsetTop,
              width: bulletSize,
              zIndex: -1,
            }}
          />
        )}
        {title}
      </NewLink>
    </span>
  )
}

const Item = ({ item, isSteps }) => {
  const { getItemState } = useSidebarContext()
  const { isActive } = getItemState(item)
  const itemRef = React.useRef(null)

  React.useEffect(() => {
    // If the active item isn't a hash, scroll to it on page load
    if (isActive && !item.link.includes(`#`)) {
      itemRef.current.scrollIntoView({ block: `center` })
    }
  }, [isActive, item])

  if (item.items) {
    return <Accordion itemRef={itemRef} item={item} />
  } else {
    return (
      <li
        sx={{
          fontSize: ["14px", "14px", "14px", "16px"],
        }}
        ref={itemRef}
      >
        <ItemLink item={item} isSteps={isSteps} />
      </li>
    )
  }
}

const isParentActive = (sections, parentTitle) => {
  for (let section of sections) {
    if (section.title === parentTitle) return section
    if (section.items) {
      for (let items of section.items) {
        const activeSubItem = isParentActive([items], parentTitle)
        if (activeSubItem) return activeSubItem
      }
    }
  }

  return false
}

// return items up the sidebar's hierarchy
const getActiveItemParents = (
  itemList,
  activeItemLink,
  activeItemParents = []
) => {
  if (activeItemLink.parentTitle) {
    const bar = isParentActive(itemList, activeItemLink.parentTitle)
    activeItemParents.push(bar)
    return getActiveItemParents(itemList, bar, activeItemParents)
  } else {
    return activeItemParents
  }
}

const isItemActive = (location, item, activeItemHash) => {
  const basePath = location.pathname
  const itemLink = `${prefix}${item.link}`
  const linkMatchesPathname = itemLink === basePath || itemLink + '/' === basePath
  const linkWithoutHashMatchesPathname =
    item.link.replace(/#.*/, ``) === basePath
  const activeItemHashFalsy = !activeItemHash || activeItemHash === `NONE`

  if (activeItemHash) {
    if (activeItemHash === `NONE` && linkWithoutHashMatchesPathname) {
      return item
    }

    if (item.link === `${basePath}#${activeItemHash}`) {
      return item
    }
  }

  if (linkMatchesPathname && activeItemHashFalsy) {
    return item
  }

  if (item.link === `${basePath}${location.hash}` && !activeItemHash) {
    return item
  }

  if (linkMatchesPathname && !location.hash && !activeItemHash) {
    return item
  }

  return false
}

// find the current active item in the sidebar
const getActiveItem = (itemList, location, activeItemHash) => {
  for (let item of itemList) {
    if (item.link) {
      if (isItemActive(location, item, activeItemHash)) return item
    }

    if (item.items) {
      let activeSubItem = getActiveItem(item.items, location, activeItemHash)
      if (activeSubItem) return activeSubItem
    }
  }

  return false
}

function isItemInActiveTree(item, activeItem, activeItemParents) {
  return (
    activeItem.title === item.title ||
    activeItemParents.some(parent => parent.title === item.title)
  )
}

function getOpenItemHash(itemList, activeItem, activeItemParents) {
  let result = {}
  for (let item of itemList) {
    if (item.items) {
      result[item.title] = isItemInActiveTree(
        item,
        activeItem,
        activeItemParents
      )
      result = {
        ...result,
        ...getOpenItemHash(item.items, activeItem, activeItemParents),
      }
    }
  }
  return result
}

const SidebarContext = React.createContext({})

export function useSidebarContext() {
  return React.useContext(SidebarContext)
}

function Sidebar({
  position,
  location,
  activeItemHash,
  disableExpandAll,
  disableAccordions,
}) {
  const itemList = menuValue
  const closeSidebar = () => {
    console.log("Closed")
  }
  const scrollRef = React.useRef(null)

  // Set the scroll position if one is provided
  React.useEffect(() => {
    if (scrollRef.current && position) {
      scrollRef.current.scrollTop = position
    }
  }, [position])

  const activeItem = React.useMemo(
    () => getActiveItem(itemList, location, activeItemHash),
    [itemList, location, activeItemHash]
  )

  const activeItemParents = React.useMemo(
    () => getActiveItemParents(itemList, activeItem),
    [itemList, activeItem]
  )

  // Get the hash where the only open items are
  // the hierarchy defined in props
  const initialHash = getOpenItemHash(itemList, activeItem, activeItemParents)

  const [openSectionHash, setOpenSectionHash] = React.useState(initialHash)
  const expandAll = Object.values(openSectionHash).every(isOpen => isOpen)

  const toggleSection = React.useCallback(item => {
    setOpenSectionHash(openSectionHash => {
      return {
        ...openSectionHash,
        [item.title]: !openSectionHash[item.title],
      }
    })
  }, [])

  const getItemState = React.useCallback(
    item => {
      return {
        isExpanded: openSectionHash[item.title] || disableAccordions,
        isActive: item.title === activeItem.title,
        inActiveTree: isItemInActiveTree(item, activeItem, activeItemParents),
      }
    },
    [openSectionHash, disableAccordions, activeItem, activeItemParents]
  )

  const context = React.useMemo(() => {
    return {
      getItemState,
      disableAccordions,
      onLinkClick: closeSidebar,
      onSectionTitleClick: toggleSection,
    }
  }, [getItemState, disableAccordions, closeSidebar, toggleSection])

  return (
    <SidebarContext.Provider value={context}>
      <section
        aria-label="Secondary Navigation"
        id="SecondaryNavigation"
        className="docSearch-sidebar"
        sx={{ height: `100%` }}
      >
        <nav
          ref={scrollRef}
          sx={{
            WebkitOverflowScrolling: `touch`,
            border: 0,
            display: `block`,
            overflowY: `auto`,
            transition: t =>
              `opacity ${t.transition.speed.default} ${t.transition.curve.default}`,
            zIndex: 10,
            borderRightWidth: `1px`,
            borderRightStyle: `solid`,
            borderColor: `ui.border`,
            height: `100%`,
            top: t => `calc(4rem + 0rem)`,
          }}
        >
          <ul
            sx={{
              m: 0,
              py: 4,
              bg: `backcolor`,
              "& li": {
                m: 0,
                listStyle: `none`,
              },
              "& > li:last-child > span:before": {
                display: `none`,
              },
            }}
          >
            {itemList.map(item => (
              <Item item={item} key={item.title} />
            ))}
          </ul>
        </nav>
      </section>
    </SidebarContext.Provider>
  )
}

export default Sidebar
