const H1 = styled.div`
  display: block;
  margin: 0;
  padding: 16px;
`

function HeadingComponent(props) {
  const { children, id, style, headingTag } = props
  const origin = children[1].split(":::")
  const fullTitle = origin[0]
  const shortTitle = origin[1]
  // const CustomTag = `${headingTag}`
  const CustomTag = Styled[headingTag]
  return (
    <>
      <CustomTag
        sx={{
          "&:hover span:not(:hover)": {
            color: `bulletColor`,
          },
        }}
        id={id}
      >
        <HeadingIcon id={id} />
        {fullTitle}
      </CustomTag>
    </>
  )
}

function Heading2Component(props) {
  return <HeadingComponent {...props} headingTag="h2" />
}
