export function Youtube({ videoId }) {
  return (
    <div
      className="videowrapper"
      sx={{ marginTop: "24px", marginBottom: "32px", marginX: "auto" }}
    >
      <iframe
        id="ytplayer"
        src={`https://www.youtube.com/embed/${videoId}?autoplay=0&origin=http://ejabu.github.io`}
        frameBorder="0"
      ></iframe>
    </div>
  )
}

const prefix =
  process.env.PATH_PREFIX == undefined ? "" : `/${process.env.PATH_PREFIX}`

export function LinkComponent(props) {
  let newHref = props.href
  if (prefix !== "") {
    newHref = props.href.replace(prefix, "")
  }
  return (
    <Link
      sx={{
        color: `text`,
        borderBottom: `1px solid`,
        borderColor: `bulletColor`,
        transition: "font-weight .1s ease, color .4s ease",
        "&:hover": {
          fontWeight: "900",
          color: "bulletColor",
          borderColor: "bulletColor",
        },
      }}
      to={newHref}
      className="highlight"
      target={props.title == "_blank" ? "_blank" : null}
    >
      {props.children}
    </Link>
  )
}
export function InlineCodeComponent(props) {
  return (
    <span
      sx={{
        bg: `bulletColor`,
        color: "white",
      }}
      className="highlight"
    >
      {props.children}
    </span>
  )
}

export function CodeComponent(props) {
  const code = "\n" + props.children

  let [language, options] = props.className.split(`{`)
  const [, codeLanguage] = language.split("-")
  return (
    <Highlight
      {...defaultProps}
      theme={theme}
      code={code}
      language={codeLanguage}
    >
      {({ className, style, tokens, getLineProps, getTokenProps }) => (
        <pre className={className} style={style}>
          {tokens.map((line, i) => (
            <div {...getLineProps({ line, key: i })}>
              {line.map((token, key) => (
                <span {...getTokenProps({ token, key })} />
              ))}
            </div>
          ))}
        </pre>
      )}
    </Highlight>
  )
}

function TestComponent(props) {
  const theme = useContext(MdxContext)
  const dirname = theme.dirname
  return <ImageData {...props} dirname={dirname} />
  return <div className="container">{JSON.stringify(props)}</div>
}

