/** @jsx jsx */
import { jsx } from "theme-ui"
import React, { useMemo } from "react"
import { useStaticQuery, graphql } from "gatsby"
import { Item } from "react-photoswipe-gallery"

const path = require("path")
const ImageData = props => {
  const smallItemStyles = {
    cursor: "pointer",
    objectFit: "cover",
    // width: "100%",
    // maxHeight: "100%",
  }
  const data = useStaticQuery(graphql`
    query {
      allFile(filter: { internal: { mediaType: { regex: "/image/" } } }) {
        edges {
          node {
            #internal {
            #    mediaType
            #}
            absolutePath
            childImageSharp {
              fluid(maxWidth: 600, srcSetBreakpoints: [600]) {
                src
                srcSet
                base64
                sizes
                aspectRatio
              }
            }
          }
        }
      }
    }
  `)

  const imageAbsolutePath = path.join(props.dirname, props.src)

  const match = useMemo(
    () =>
      data.allFile.edges.find(
        ({ node }) => imageAbsolutePath === node.absolutePath
      ),
    [data, imageAbsolutePath]
  )
  const imageTitle = props.alt
  if (match === undefined) {
    return false
  }

  const { aspectRatio, srcSet, src } = match.node.childImageSharp.fluid
  const images = srcSet.split(",\n")
  const originalImage = images[images.length - 1].split(" ")
  const originalImageSrc = originalImage[0]
  const width = parseInt(originalImage[1].slice(0, -1))
  const height = Math.ceil(width / aspectRatio)
  return (
    <div sx={{ marginBottom: "24px", fontSize: "14px", color: "#8a8a8a" }}>
      <div className="center-box">
        <Item
          original={originalImageSrc}
          thumbnail={src}
          width={width}
          height={height}
          title={imageTitle}
        >
          {({ ref, open }) => (
            <img style={smallItemStyles} src={src} ref={ref} onClick={open} />
          )}
        </Item>
      </div>
      <div className="center-box">
        {imageTitle}
      </div>
    </div>
  )
}

export default ImageData
